.wrapper {
  background: 
  /* top, transparent chartreuse, faked with gradient */ 
  -webkit-gradient(
    linear, 
    left top, left bottom, 
    from(rgb(180, 208, 103, .9)), 
    to(rgb(180, 208, 103, .9))
  ),
  /* bottom, image */
  url("http://www.carasaven.com/wp-content/uploads/2017/02/CaraSaven-BananaLeavesGraphic-Wallpaper.jpg");
  background: 
  /* top, transparent chartreuse, faked with gradient */ 
  -webkit-linear-gradient(
    rgb(180, 208, 103, .9), 
    rgb(180, 208, 103, .9)
  ),
  /* bottom, image */
  url("http://www.carasaven.com/wp-content/uploads/2017/02/CaraSaven-BananaLeavesGraphic-Wallpaper.jpg");
  background: 
  /* top, transparent chartreuse, faked with gradient */ 
  -o-linear-gradient(
    rgb(180, 208, 103, .9), 
    rgb(180, 208, 103, .9)
  ),
  /* bottom, image */
  url("http://www.carasaven.com/wp-content/uploads/2017/02/CaraSaven-BananaLeavesGraphic-Wallpaper.jpg");
  background: 
  /* top, transparent chartreuse, faked with gradient */ 
  linear-gradient(
    rgb(180, 208, 103, .9), 
    rgb(180, 208, 103, .9)
  ),
  /* bottom, image */
  url("http://www.carasaven.com/wp-content/uploads/2017/02/CaraSaven-BananaLeavesGraphic-Wallpaper.jpg");
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  overflow: auto;
}



